
import React from 'react'
import styled from 'styled-components'


const InfoContainer = styled.div`
    background-color : rgba(0,0,0, 0.3);
    padding : 20px;
`

const Data = styled.div`
    margin-top : 10px;
    padding : 10px 10px;
    background-color: rgba(24, 24, 28, 0.85);
`




const More = ({result}) => (
    <>
    {console.log(result)}
        <InfoContainer>
            Homepage
            <Data >{result.homepage}</Data>        
        </InfoContainer>
        <InfoContainer>
            Release Date
            <Data >{result.release_date? result.release_date : result.first_air_date}</Data>        
        </InfoContainer>
        {result.budget? (<InfoContainer>
            Budget
            <Data >{result.budget}</Data>        
        </InfoContainer>) : null}
        
        {result.spoken_languages? (
            <InfoContainer>
            Spoken Lanuage
            {result.spoken_languages.map((language, index)=> (
                <Data key = {index}>{language.name}</Data>
            ))}        
            </InfoContainer>) : null}
        
    </>
)


export default More
