import React from 'react'
import TabPresenter from './TabPresenter'
import {movieApi, tvApi} from '../../api'
export default class extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            cast : null,
            result : this.props.result,
            isMovie : this.props.isMovie
        }
    }
    

    componentDidMount () {
        this.getCast()
    }
    async getCast() {
        try {
            const _id = this.props.result.id
            const parsedId = parseInt(_id)

            const isMovie = this.props.isMovie
            const {data:{cast}} = (isMovie)? await movieApi.cast(parsedId) : await tvApi.cast(parsedId)
            this.setState({
                cast : cast
            })
            
            
        } catch (e){
            this.setState({
                error : "cant find information on detail"
            })
        } 
    }
    render () {
        const {result, isMovie, cast} = this.state
        return <TabPresenter result = {result} isMovie = {isMovie} cast = {cast}/>
    }
}